import React from "react";
import { withRouter } from "react-router";
import { Link } from "react-router-dom";
import { Tab, Tabs } from "@material-ui/core";
import { routes } from '../../consts'
import { withStyles } from "@material-ui/styles";
import styles from "./styles";

const NavBar = props => {
    const { classes } = props;
    const isNotFound = props.location.pathname !== "/404";
    let root = () => process.env.PUBLIC_URL;

    return isNotFound ? (
        <Tabs className={classes.tabs} centered={true}>
            <Tab className={classes.tab} label="About" component={Link} to={`${root()}${routes.about}`} />
            <Tab
                className={classes.tab}
                label="Counters" 
                component={Link}
                to={`${root()}${routes.counters}`}
            />
            <Tab className={classes.tab} label="Login" component={Link} to={`${root()}${routes.login}`} />
            <Tab
                className={classes.tab}
                label="Login with Redux"
                component={Link}
                to={`${root()}${routes.loginRedux}`}
            />
        </Tabs>
    ) : null;
};

export default withRouter(withStyles(styles)(NavBar));